import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faItchIo, faGithub } from '@fortawesome/free-brands-svg-icons'

import Layout from "../layouts/_main"
import SteampunkGumball from "../images/SteampunkGumball.png"
import Babilawn from "../images/Babilawn.png"
import Pandamic from "../images/Pandamic.png"
import GGJ from "../images/GGJ_Logo.svg"

class Portfolio extends React.Component {
    render() {
        return (
            <Layout>
                <div id="portfolioBanner" className="jumbotron jumbotron-fluid bg-transparent text-light heroImage heroImage3">
                    <div className="container-fluid text-center">
                        <h1 className="display-3">Portfolio</h1>
                    </div>
                </div>
                <div className="container pb-4">
                    <ul class="list-unstyled">
                        <li class="media">
                            <img src={Pandamic} class="mr-3 img-thumbnail" alt="Pandamic" style={{ width: "400px" }} />
                            <div class="media-body">
                                <h3 class="mt-0 mb-1">Pandamic</h3>
                                <p>
                                    [Coming Soon]
                                </p>
                                <a className="px-2 pl-1" href="https://jordandubemedia.itch.io/pandamic" style={{ color: "#fa5c5c" }}>
                                    <FontAwesomeIcon icon={faItchIo} size="2x" color="#fa5c5c" /> Itch.io
                                    </a>
                                <a className="px-2 pl-1 text-dark" href="https://github.com/Rorazoro/GDEX2020GameJam">
                                    <FontAwesomeIcon icon={faGithub} size="2x" /> Github
                                </a>
                            </div>
                        </li>
                        <li class="media my-4">
                            <div class="media-body">
                                <h3 class="mt-0 mb-1">Babilawn</h3>
                                <p>
                                    [Coming Soon]
                                </p>
                                <a className="px-2 pl-1" href="https://jam-force.itch.io/babilawn" style={{ color: "#fa5c5c" }}>
                                    <FontAwesomeIcon icon={faItchIo} size="2x" color="#fa5c5c" /> Itch.io
                                    </a>
                                <a className="px-2 pl-1 text-dark" href="https://github.com/Rorazoro/ExtraCreditsGameJam6">
                                    <FontAwesomeIcon icon={faGithub} size="2x" /> Github
                                </a>
                            </div>
                            <img src={Babilawn} class="mr-3 img-thumbnail" alt="Babilawn" style={{ width: "400px" }} />
                        </li>
                        <li class="media">
                            <img src={SteampunkGumball} class="mr-3 img-thumbnail" alt="Steampunk Gumball" style={{ width: "400px" }} />
                            <div class="media-body">
                                <h3 class="mt-0 mb-1">Steampunk Gumball</h3>
                                <p>
                                    [Coming Soon]
                                </p>
                                <a className="px-2 pl-1" href="https://globalgamejam.org/2020/games/steampunk-gumball-7" style={{ color: "#41B6E6" }}>
                                    {/* <FontAwesomeIcon icon={faItchIo} size="2x" color="#fa5c5c" /> Itch.io */}
                                    <img className="img-fluid align-top" src={GGJ} alt="Global Game Jam" style={{ width: "2.8em" }} /> GGJ
                                </a>
                                <a className="px-2 pl-1 text-dark" href="https://github.com/Rorazoro/GGJ2020-SteampunkGumball">
                                    <FontAwesomeIcon className="align-top" icon={faGithub} size="2x" /> Github
                                </a>
                            </div>
                        </li>
                    </ul>
                </div>
            </Layout>
        )
    }
}

export default Portfolio